<template lang="pug">
  div(class="home-root")
    //- div(id="PlanetScene")
    user-panel(
      :user="user"
      :planet="planet"
      :mode="'Home'"
      @openModal="openModal")
    h2 {{planet.name}}
      button(@click="openModal('planet-info')" class="show-info-btn")
    span ({{planet.positionX}},{{planet.positionY}})
    router-link(tag="button" to="/game" class="enter button-default") 進入星球
    modal-card(v-if="isModalOpen" @close="isModalOpen = !isModalOpen")
      planet-info(
        v-if="currentOpen === 'planet-info'"
        slot="card"
        :user="user"
        :planet="planet")
      user-profile(v-if="currentOpen === 'user-profile'" slot="card" :user="user")
</template>

<script>
import UserPanel from '@/components/user/UserPanel.vue'
import ModalCard from '@/components/ModalCard.vue'
import PlanetInfo from '@/components/home/PlanetInfo.vue'
import UserProfile from '@/components/user/UserProfile.vue'
import { mapState, mapMutations } from 'vuex'

export default {
  components: {
    UserPanel,
    ModalCard,
    PlanetInfo,
    UserProfile
  },
  data: function () {
    return {
      isModalOpen: false,
      currentOpen: ''
    }
  },
  beforeMount () {
    this.$socket.emit('login', this.user.uid)
  },
  methods: {
    ...mapMutations('user', ['SET_ISLOADING']),
    openModal (one) {
      this.isModalOpen = true
      this.currentOpen = one
    }
  },
  computed: {
    ...mapState('user', {
      user: state => state
    }),
    ...mapState('planet', {
      planet: state => state.planet
    })
  }
}
</script>

